html, body, #root {
  height: 100%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.nav-link {
  font-size: 13px;
}

.h-500 {
  height: 500px;
}

.f-14 {
  font-size: 14px;
}

.intro {
  width: 80%;
  border-bottom: 1px solid #ccc;
}

.greeting {
  width: 80%;
}

.intro h3 {
  margin-bottom: 5px;
}

.skills {
  font-size: 12px;
  font-weight: 500;
  letter-spacing: 0.5px;
  padding: 5px 0;
}

.greeting h4 {
  padding: 7px 0;  
}

.greeting p {
  margin: 5px 0;
}

.cv-button {
  width: 70px;
}

.education {
  width: 75%;
}

.edu-card {
  width: 100%;
}

.css-xy7s13-TimelineContentDetailsWrapper {
  align-items:flex-start!important;
}

.edu-card-subheading p {
  margin: 0;
}

.service-card .card-body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.service-card .card-body img {
  height: 80px;
}

.service-card .card-text {
  color: #555;
  font-size: 13px;
}

.services {
  width: 75%;
}

.footer {
  padding: 20px 0;
  max-width: 100%;
  border-top: 1px solid #eee;
}

.contact-field {
  border: 0 !important;
  border-bottom: 1px solid #ccc !important;
  border-radius: 0 !important;
  outline: none!important;
  transition: 0.3 all ease-in-out;
}

.contact-field:active, .contact-field:focus {
  outline: none !important;
  box-shadow: none !important;
  border-bottom: 1px solid #0078bf !important;
}

.links {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.links .card {
  border: 0!important;
}

.send-button {
  border-radius: 20px !important;
}